import React, { useRef } from "react";
import Lottie, { LottieRefCurrentProps } from "lottie-react";
import AnimationData from "../Animation - 1723034242780.json";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

const Header = () => {
  const git = useRef<LottieRefCurrentProps>(null);

  return (
    <Container>
      <Row >
        <Col  data-aos-duration="2000" data-aos="fade-right" className="money" md={6}>
          <h1 id='home' className="text-light poppins-regular text-center mt-4 mb-0">
            Make a{" "}
            <span className="text-warning display-2 fw-bold style-script-regular">
              Money
            </span>{" "}
            by <i className="bi bi-currency-exchange mx-2 text-warning"></i>{" "}
            CryptoWave.
          </h1>
        </Col>
        <Col data-aos-duration="1600" data-aos="fade-left" className="money">
          <Lottie
            onComplete={() => {
              git.current?.setDirection(-1);
              git.current?.play();
            }}
            loop={true}
            className="lottie"
            lottieRef={git}
            animationData={AnimationData}
          />
        </Col>
      </Row>
    </Container>
  );
};

export default Header;
