import React, { useEffect, useState } from "react";
import axios from "./axios"; // Ensure this is correctly configured
import { useNavigate } from "react-router-dom";
import Brokers from "./Brokers";

const InputBox = () => {
  const [input, setInput] = useState("");
  const [cryptoDatas, setCryptoData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const navigate = useNavigate();

  const fetchApi = async () => {
    setLoading(true);
    setError(null);
    try {
      const storedData = sessionStorage.getItem("cryptoData");
      const storedTimestamp = sessionStorage.getItem("cryptoDataTimestamp");
      const currentTime = new Date().getTime();

      if (
        storedData &&
        storedTimestamp &&
        currentTime - storedTimestamp < 5 * 60 * 1000
      ) {
        setCryptoData(JSON.parse(storedData));
      } else {
        const response = await axios.get(`/coins/markets?vs_currency=usd`);
        setCryptoData(response.data);
        sessionStorage.setItem("cryptoData", JSON.stringify(response.data));
        sessionStorage.setItem("cryptoDataTimestamp", currentTime);
      }
    } catch (error) {
      setError("Failed to fetch data. Please try again later.");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchApi();

    const intervalId = setInterval(fetchApi, 3 * 60 * 1000); // Fetch data periodically

    return () => clearInterval(intervalId); // Cleanup interval on unmount
  }, []);

  const filteredData = cryptoDatas.filter((cryptoData) =>
    cryptoData.name.toLowerCase().includes(input.toLowerCase())
  );

  const handleTradeClick = (coin) => {
    navigate(`/next/${coin.id}`);
  };

  return (
    <>
      <div className="inputBox-Div">
        <input
          className="inputBox"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder=" Search"
        />
      </div>
      <div id="view" data-aos="fade-up" data-aos-duration="1000" className="tableMain">
        <div className="table-container">
          {loading ? (
            <p>Loading...</p>
          ) : error ? (
            <p>{error}</p>
          ) : (
            <table className="crypto-table">
              <thead>
                <tr>
                  <th>#</th>
                  <th></th>
                  <th>Currency</th>
                  <th>Price</th>
                  <th>Change</th>
                  <th>Status</th>
                  <th>Trade</th>
                </tr>
              </thead>
              <tbody>
                {filteredData.length > 0 ? (
                  filteredData.slice(0, 10).map((coin, index) => (
                    <tr key={coin.id}>
                      <td>{index + 1}</td>
                      <td>
                        <img
                          className="imghw"
                          src={coin.image}
                          alt={coin.name}
                        />
                      </td>
                      <td>{coin.name}</td>
                      <td>${coin.current_price.toLocaleString()}</td>
                      <td
                        className={
                          coin.price_change_percentage_24h >= 0
                            ? "upgrapg fw-bold"
                            : "downgrapg fw-bold"
                        }
                      >
                        {coin.price_change_percentage_24h.toFixed(2)}%
                      </td>
                      <td>
                        {coin.price_change_percentage_24h <= 0 ? (
                          <i className="bi bi-graph-down-arrow downgrapg"></i>
                        ) : (
                          <i className="upgrapg bi bi-graph-up-arrow"></i>
                        )}
                      </td>
                      <td>
                        <button
                          className="button-56"
                          role="button"
                          onClick={() => handleTradeClick(coin)}
                        >
                          Trade
                        </button>
                      </td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan="7" style={{ textAlign: "center" }}>
                      No results found
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          )}
        </div>
      </div>
      <Brokers />
    </>
  );
};

export default InputBox;
