import React, { useEffect, useState } from "react";
import axios from "./axios";
import { useParams } from "react-router-dom";
import { Chart } from "react-google-charts";
import ClipLoader from "react-spinners/ClipLoader"; // Import the spinner
import Brokers from "./Brokers";

const NextPage = () => {
  const { id } = useParams();
  const [chartV, setChartV] = useState([]);
  const [chartV1, setChartV1] = useState("");
  const [loading, setLoading] = useState(true); // Start with loading true
  const [error, setError] = useState(false);

  const fetchChart = async () => {
    setLoading(true);
    setError(false);
    try {
      const storedChartData = sessionStorage.getItem(`chartData_${id}`);
      const storedChartTimestamp = sessionStorage.getItem(
        `chartDataTimestamp_${id}`
      );
      const currentTime = new Date().getTime();

      if (
        storedChartData &&
        storedChartTimestamp &&
        currentTime - storedChartTimestamp < 5 * 60 * 1000
      ) {
        const parsedChartData = JSON.parse(storedChartData);
        setChartV(parsedChartData.prices);
        setChartV1(parsedChartData.info);
      } else {
        const [response2, response3] = await Promise.all([
          axios.get(`/coins/${id}/market_chart?vs_currency=usd&days=10`),
          axios.get(`/coins/markets?vs_currency=usd&ids=${id}`),
        ]);

        const fetchedData = {
          prices: response2.data.prices,
          info: response3.data[0],
        };

        setChartV(fetchedData.prices);
        setChartV1(fetchedData.info);

        sessionStorage.setItem(`chartData_${id}`, JSON.stringify(fetchedData));
        sessionStorage.setItem(
          `chartDataTimestamp_${id}`,
          currentTime.toString()
        );
      }
    } catch (error) {
      setError(true);
    } finally {
      setLoading(false); // Ensure loading is set to false regardless of error
    }
  };

  useEffect(() => {
    fetchChart();

    const intervalId = setInterval(() => {
      fetchChart();
    }, 3 * 60 * 1000);

    return () => clearInterval(intervalId);
  }, [id]);

  if (loading) {
    return (
      <div className="loader-container">
        <ClipLoader color={"#ffc107"} loading={loading} size={60} />
      </div>
    );
  }

  if (error) {
    return (
      <div className="loader-container">
        <ClipLoader color={"#ffc107"} loading={true} size={60} />{" "}
        {/* Display loader when there is an error */}
      </div>
    );
  }

  if (!chartV.length) {
    return <p>No chart data available</p>;
  }

  const chartData = [
    ["Time", "Price (USD)"],
    ...chartV.map(([timestamp, price]) => [new Date(timestamp * 1000), price]),
  ];

  const options = {
    title: `${id.toUpperCase()} Price Chart`,
    curveType: "function",
    legend: { position: "bottom" },
    hAxis: { title: "Time" },
    vAxis: { title: "Price (USD)" },
    backgroundColor: "#f4f4f4",
  };

  return (
    <>
      <div className="nextPageDiv">
        <div className="imgDiv">
          <img
            data-aos="flip-left"
            data-aos-duration="1500"
            src={chartV1.image}
            alt={`${id} logo`}
          />
          <h1 data-aos="zoom-in" data-aos-duration="1600">
            {id.toUpperCase()}
          </h1>
        </div>
        <div className="chartDiv">
          <Chart
            data-aos="zoom-in"
            data-aos-duration="1600"
            key={JSON.stringify(chartData)} // Force re-render when chartData changes
            className="chartDiv1"
            chartType="LineChart"
            data={chartData}
            options={options}
          />
        </div>
        <div
          data-aos="fade-up"
          data-aos-duration="1000"
          className="aboutCoinDiv"
        >
          <div className="aboutCoin1">
            <h4>Crypto Market Rank:</h4>
            <h4>{chartV1.market_cap_rank || "N/A"}</h4>
          </div>
          <div className="aboutCoin2">
            <h4>Currency Price:</h4>
            <h4>${chartV1.current_price?.toLocaleString() || "N/A"}</h4>
          </div>
          <div className="aboutCoin3">
            <h4>Market Cap:</h4>
            <h4>${chartV1.market_cap?.toLocaleString() || "N/A"}</h4>
          </div>
          <div className="aboutCoin4">
            <h4>24Hr High:</h4>
            <h4>${chartV1.high_24h?.toLocaleString() || "N/A"}</h4>
          </div>
          <div className="aboutCoin5">
            <h4>24Hr Low:</h4>
            <h4>${chartV1.low_24h?.toLocaleString() || "N/A"}</h4>
          </div>
        </div>
      </div>
      <Brokers />
    </>
  );
};

export default NextPage;
