import React, { useState } from "react";
import "bootstrap-icons/font/bootstrap-icons.css";

const NavBar = () => {
  const [down, setDown] = useState(true);

  const toggle = () => {
    setDown(!down);
  };

  return (
    <nav className="navbar navbar-expand-lg">
      <div className="container-fluid">
        <a className="navbar-brand text-light fs-3 luxurious-roman-regular" href="#">
          <i className="bi bi-currency-exchange mx-2 text-warning"></i>CryptoWave
        </a>
        <button
          onClick={toggle}
          className="navbar-toggler border border-2"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNavAltMarkup"
          aria-controls="navbarNavAltMarkup"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          {down ? (
            <i className="bi bi-arrow-down text-light"></i>
          ) : (
            <i className="bi bi-arrow-up text-light"></i>
          )}
        </button>
        <div className="collapse navbar-collapse justify-content-end" id="navbarNavAltMarkup">
          <div className="navbar-nav">
            <a className="nav-link active text-light" aria-current="page" href="#home">
              Home <i className="bi bi-house-check"></i>
            </a>
            <a className="nav-link text-light" href="#view">
              View <i className="bi bi-view-list"></i>
            </a>
            <a className="nav-link text-light" href="#brokers">
              Brokers <i className="bi bi-person-video2"></i>
            </a>
          </div>
        </div>
      </div>
    </nav>
  );
};

export default NavBar;
